</template>

<script>
import carousel from 'vue-owl-carousel'
import ImageFile from '@/files/Images.json';
import router from "@/router";
export default {
  name: "Sols",
  components:{
    carousel,
  },
  data() {
    return {
      imageFile: [],
    }
  },
  methods: {
    getImageFile() {
      if (router.currentRoute.name === 'Sols'){
        this.imageFile = ImageFile.sols;
      }else {
        this.imageFile = ImageFile.murs;
      }
      console.log(this.imageFile);
    },
  },
  created: function () {
    this.getImageFile()
  },
}
</script>

