<template>
  <div>
    <div>
      <svg class="ham hamRotate ham8" id="ham" viewBox="0 0 100 100" width="80" @click="toggleMenu()">
        <path
            class="line top"
            d="m 30,33 h 40 c 3.722839,0 7.5,3.126468 7.5,8.578427 0,5.451959 -2.727029,8.421573 -7.5,8.421573 h -20"/>
        <path
            class="line middle"
            d="m 30,50 h 40"/>
        <path
            class="line bottom"
            d="m 70,67 h -40 c 0,0 -7.5,-0.802118 -7.5,-8.365747 0,-7.563629 7.5,-8.634253 7.5,-8.634253 h 20"/>
      </svg>
    </div>


    <div id="sidenavMenu" class="sidenav">
      <div class="list-menu">
        <ul>
          <li>
            <router-link to="/" class="links-animation">
              <div v-on:click="toggleMenu" class="links">
                <img :src="require('@/assets/Navigation/home.png')" alt="">
                <span>Accueil</span>
              </div>
            </router-link>
          </li>
          <li>
            <router-link to="/services" class="links-animation">
              <div v-on:click="toggleMenu" class="links">
                <img :src="require('@/assets/Navigation/service.png')" alt="">
                <span>Services</span>
              </div>
            </router-link>
          </li>
          <li>
            <router-link to="/contact" class="links-animation">
              <div v-on:click="toggleMenu" class="links">
                <img :src="require('@/assets/Navigation/phone.png')" alt="">

                <span>Contact</span>
              </div>
            </router-link>
          </li>
          <!--
          <li>
            <router-link to="/apropos" class="links-animation">
              <div v-on:click="toggleMenu" class="links">
                <img :src="require('src/assets/Navigation/ask.png')" alt="">

                <span>À propos</span>
              </div>
            </router-link>
          </li>
          -->
        </ul>
      </div>
      <div class="logo-menu">
        <img :src="require(`@/assets/Accueil/acpintologo.png`)" alt="logo AC Pinto">
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: "Navigation",
  data() {
    return {
      isActive: false
    }
  },
  methods: {
    toggleMenu: function () {
      this.isActive = !this.isActive;
      const sidenav = document.getElementById("sidenavMenu");
      const plates = document.getElementById("ham");
      const links = document.getElementsByClassName('links');
      if (!this.isActive) {
        plates.classList.remove('active');
        for (let i = 0; i < links.length; i++) {
          links.item(i).classList.remove("text-focus-in");
          links.item(i).classList.add("text-blur-out");
        }
        setTimeout(() => {
          sidenav.style.width = "0";
        }, 400);
      } else {
        plates.classList.add('active');
        sidenav.style.width = "100%";
        if (sidenav.style.width === '100%') {
          for (let i = 0; i < links.length; i++) {
            links.item(i).classList.remove("text-blur-out");
            links.item(i).classList.add("text-focus-in");
          }
        }
      }
    },
  }

}

</script>
